

















	export default {
		data() {
			return {
				SessionKey: '',
				OpenId: '',
				nickName: null,
				avatarUrl: null,
				isCanUse: uni.getStorageSync('isCanUse')||true//默认为true
			};
		},
		methods:{
			 loginFn() {
			    let _this = this;
			    uni.showLoading({
			        title: '登录中...'
			    });
			   // 1.wx获取登录用户code
			    uni.login({
			        provider: 'weixin',
			        success: function(loginRes) {
			            let code = loginRes.code;
						uni.showToast({
							title: code,
							icon: "none"
						});
						_this.mPost('/auth//wxLogin',{ //存储登录code
							code:code
						}).then(res=>{
							uni.showToast({
									title:JSON.stringify(res)
							})
							if(res.code=1){
								if(res.data){
									let data = res.data;
									uni.setStorageSync('authToken', data.authToken);
									uni.showToast({
										title:  'authToken'+uni.getStorageSync('authToken'),
										icon: "none"
									});
								}
							}
							uni.hideLoading();
						}).catch(err=>{
							console.log(err);
							uni.hideLoading();
						});
			        },
			    });
			},
			//第一授权获取用户信息===》按钮触发
			wxGetUserInfo:function(res){
				let _this = this;
				if (!res.detail.iv) {
					uni.showToast({
						title: "您取消了授权,登录失败",
						icon: "none"
					});
					return false;
				}
				uni.getUserInfo({
			        provider: 'weixin',
			        success: function(infoRes) {
						_this.mPost('/auth/wxUserInfo',{// 	储存用户信息
							...infoRes.userInfo
						}).then(res=>{
							uni.showToast({
									title:JSON.stringify(res)
							})
							if(res.code=1){
								if(res.data){
									let data = res.data;
									uni.setStorageSync('authToken', data.authToken);
									uni.showToast({
										title:  'authToken'+uni.getStorageSync('authToken'),
										icon: "none"
									});
								}
							}
							uni.hideLoading();
						}).catch(err=>{
							console.log(err);
							uni.hideLoading();
						});
			        },
			        fail(res) {}
			    });
			},
			getPhoneNumber: function(e) {  
                console.log(e);  
                if (e.detail.errMsg == 'getPhoneNumber:fail user deny') {  

                } else {  

                }  
            },  
			 //向后台更新信息
			updateUserInfo() {
			}
		},
		onLoad(){
			this.loginFn()
		}
	}
